

html {
    padding: 0.5% 0.5%;
    scroll-behavior: smooth;
  }

body {
    background-image: url(/src/img/spaceBG.gif);
}
#MainLayout {
   width: 100%;
   height: auto;
}

/*HEADER*/
#Header {
    background-image: url(/src/img/rain.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    height: clamp(200px, 25vw, 200px);
}
#Header h1 {
    font-family: 'CyberX-font', sans-serif;
    font-size: clamp(35px, 6.8vw, 60px);
    text-shadow: 4px 4px 2px #000000;
    text-align: center;
    color: white;
}

/*NAVBAR*/
#NavBar {
    background-image: url(/src/img/sunset.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    position: sticky;
    align-items: center; /*Center Rows*/
    justify-content: space-evenly; /*Center Columns*/
    top: 0;
    width: 100%;
    height: 5vw;
    min-height: 50px;
    border-radius: 25px;
    outline: 5px solid black;
}
#NavBar .item {
    font-family: 'CyberX-font', sans-serif;
    font-size: clamp(20px, 3vw, 3vw); 
    text-shadow: 2px 2px 4px #000000;
    color: white;
}
#NavBar .item:hover {
    color: violet;
}
#NavBar .itemHome {
    border-radius: 25px;
    margin-right: 4vw;
    width: 10%;
    height: clamp(50px, 5vw, 5vw);
}
#NavBar .itemHome:hover {
    background-color: rgb(0,0,100,0.50);
    filter: invert(1);
}

/*SIDE PANEL*/
#SidePanel {
    background: linear-gradient(180deg, rgba(55,0,164,0.7540603248259861) 50%, rgba(0,0,0,0.6519721577726219) 78%);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    align-items: center; /*Center Rows*/
    width: 100%;
    height: 65vh;
    border-radius: 50px;
}
#SidePanel #Planet {
    opacity: 70%;
    width: 12.5vw;
    width: clamp(150px, 16vw, 250px);
    height: auto;
}
#SidePanel div {
    font-family: 'PF Tempesta Seven', sans-serif;
    font-size: clamp(12px, 2vw, 18px);
    text-align: left;
    text-shadow: 2px 2px 2px #000000;
    color: white;
    overflow-y: auto;
    z-index: 11;
    margin: -20px 0;
    padding-left: 10px;
    width: clamp(150px, 16vw, 250px);
    max-height: 100%;
}
#SidePanel #Ship {
    width: clamp(75px, 8vw, 125px);
    height: auto;
}

/*HOME*/
#Home {
    background-image: url(/src/img/Computer.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    width: 100%;
    height: 50vw;
    min-height: 500px;
    padding: 5vw 0;
    margin-left: 0;
    text-align: center;
    color: white;
}
#Home h1 {
    background-color: rgb(0,0,0,0.50);;
    width: 80%;
    height: auto;
    font-family: 'CyberX-font', sans-serif;
    font-size: clamp(60px, 10vw, 100px);
    text-align: center;
    text-shadow: 5px 5px 2px #000000;
    color: white;
}

/*SATURDAY SECTION*/
#Saturday {
    background: rgb(129,23,181);
    background: linear-gradient(328deg, rgba(129,23,181,0.6643032212885154) 15%, rgba(13,168,177,0.7511379551820728) 62%, rgba(255,0,104,0.6278886554621849) 100%);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    width: 100%;
    height: auto;
    padding: 5vw 0;
    margin-left: 0;
    text-align: center;
    color: white;
}
#Saturday h1 {
    font-family: 'CyberX-font', sans-serif;
    font-size: clamp(45px, 6.8vw, 75px); 
    text-shadow: 5px 5px 2px #000000;
}
#Saturday p {
    font-family: 'PF Tempesta Seven', sans-serif;
    font-size: clamp(12px, 2vw, 18px);
    text-shadow: 2px 2px 2px #000000;
    width: 80%;   
}
#Saturday img {
    display: block;
    min-width: 80%;
    max-width: 80%;
    box-shadow: 4px 4px 15px #000000;  
}

/*SUNDAY SECTION*/
#Sunday {
    background: rgb(129,23,181);
    background: linear-gradient(149deg, rgba(129,23,181,0.6643032212885154) 15%, rgba(13,168,177,0.7511379551820728) 62%, rgba(255,0,104,0.6278886554621849) 100%);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    width: 100%;
    height: auto;
    padding: 5vw 0;
    margin-left: 0;
    text-align: center;
    color: white;
}
#Sunday h1 {
    font-family: 'CyberX-font', sans-serif;
    font-size: clamp(45px, 6.8vw, 75px);
    text-shadow: 5px 5px 2px #000000; 
}
#Sunday p {
    font-family: 'PF Tempesta Seven', sans-serif;
    font-size: clamp(12px, 2vw, 18px);
    text-shadow: 2px 2px 2px #000000;
    width: 80%;   
}
#Sunday img {
    display: block;
    min-width: 80%;
    max-width: 80%; 
    box-shadow: 4px 4px 15px #000000;   
}

/*FOOTER*/
#Footer {
    /* background-image: url('/src/img/footer.gif');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain; */
    display: flex;
    align-items: end; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    width: 100vw;
    height: clamp(50px, 15vw, 100px);
    padding-bottom: 1%;
}
#Footer a {
    font-family: 'PF Tempesta Seven', sans-serif;
    font-size: clamp(8px, 2vw, 15px);
    text-shadow: 2px 2px 2px #000000;
    color: white;
}

/*TOGGLE BUTTON*/

#ToggleOverlay {
    display: flex;
    position: sticky;
    bottom: 1vh;
    align-items: center; /*Center Rows*/
    justify-content: center; /*Center Columns*/
    width: 100%;
    margin: 5% 0 2.5% 0;
}
#popover-notes {
    background-color: rgb(0,0,0,0.25);
    border-color: white;
    border-radius: 50px;
}
.btn-primary {
    background-image: url(/src/img/nova.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 168%;
    border-radius: 50% !important;
    -webkit-border-radius: 50% !important;
    -webkit-border-radius: 50% !important;
    width: 6vw;
    min-width: 50px;
    height: 6vw;
    min-height: 50px;
}
.btn-primary:hover {
    filter: invert(1);
}
